
#uicore-animate{position:relative;background:#fff;border:1px solid #e5e7eb;box-shadow:0 4px 4px hsla(0,0%,68.2%,.25),0 9px 40px -15px hsla(0,0%,68.2%,.25);border-radius:6px;width:100%;max-width:500px;color:#0f172a;margin-top:30px;padding:20px 35px 45px
}
#uicore-animate .uianim-notification{position:absolute;top:20px;right:20px;background:#532df5;color:#fff;padding:10px 15px;border-radius:6px;text-align:center;animation:uianim-notification .4s cubic-bezier(.165,.84,.44,1.2) forwards
}
#uicore-animate .uianim-notification.uianim-error{background:red
}
@keyframes uianim-notification{
0%{opacity:0;transform:translateY(10px)
}
to{opacity:1;transform:translateY(0)
}
}
#uicore-animate .uianim-btn{display:inline-block;background:#532df5;color:#fff;padding:10px 15px;border-radius:6px;cursor:pointer;transition:all .3s ease
}
#uicore-animate .uianim-btn:hover{background:#340be4
}
#uicore-animate label{font-size:12px;color:#0f172a;font-weight:500;display:inline-block;margin-bottom:3px
}
#uicore-animate input,#uicore-animate select{margin:0;border-radius:6px;font-size:14px;width:100%;max-width:500px;box-shadow:none;color:#0f172a;background:#fff;border:1px solid #cbd5e1;outline:none;padding:8px 16px;height:auto
}
#uicore-animate .uianim-check{position:absolute;top:0;left:0;border-radius:3px;height:15px;width:15px;background:#fff;border:1px solid #cbd5e1
}
#uicore-animate .uianim-check:after{content:"";position:absolute;display:none;left:5.5px;top:2px;width:3px;height:8px;border:solid #fff;border-width:0 2px 2px 0;transform:rotate(45deg)
}
#uicore-animate .uianim-checkbox{display:block;position:relative;padding-left:23px;cursor:pointer;line-height:15px;margin-top:20px
}
#uicore-animate .uianim-checkbox input{position:absolute;z-index:-1;opacity:0
}
#uicore-animate .uianim-checkbox input:checked~.uianim-check{background:#334155
}
#uicore-animate .uianim-checkbox input:checked~.uianim-check:after{display:block
}
#uicore-animate .uianim-settings{display:flex;flex-direction:column;margin:20px 0;padding:20px 0;border-top:1px solid #e8e8e8;border-bottom:1px solid #e8e8e8
}